@import url(partials/reset.css);

@import url(partials/fonts.css);

/* my style */

a {
  text-decoration: none;
}

*:focus {
  outline: none;
}

html {
  width: 100%;
  height: 100%;
}

body {
  font-family: "ArnoPro", sans-serif;
  margin: 0;
  font-size: 1vw;
  color: #FFFFFF;
  background: none transparent;
  background: url("../images/bg.jpg") center center no-repeat;
}

.radio {
  display: block;
  position: relative;
}

.radio input {
  display: none;
}

.radio__checkbox {
  position: absolute;
  width: 3.28168vw;
  height: 3.28168vw;
  border-radius: 100%;
  box-sizing: border-box;
  border: 2px solid #d62327;
  left: -6.19872vw;
  top: 0.45579vw;
  cursor: pointer;
}

.radio input:checked ~ .radio__checkbox::after {
  content: "";
  width: 1.45852vw;
  height: 1.45852vw;
  display: block;
  border-radius: 100%;
  background: #d62327;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
  box-shadow: 0 0 0.54695vw 5px #d6232787;
}

.share__title {
  color: #ffffff;
  font-size: 2.18778vw;
  line-height: 6.56335vw;
  text-transform: uppercase;
  text-align: center;
}

.share__socials {
  margin-top: 2.27894vw;
  display: flex;
  justify-content: center;
}

.share__soc-link {
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  border: 0;
  box-sizing: border-box;
  background: #d62327;
  border-radius: 0.36463vw;
  margin: 0 0.72926vw;
  color: #FFFFFF;
  width: 4.37557vw;
  height: 4.37557vw;
}

.share__soc-link svg {
  width: 2.73473vw;
  height: 2.73473vw;
  display: block;
}

.share__soc-link:hover {
  color: #000000;
}

.test {
  padding-bottom: 3.37284vw;
  box-sizing: border-box;
  width: 100%;
}

.test__inner {
  box-sizing: border-box;
  width: 100%;
  border: 2px solid #d62327;
  background: rgba(0, 0, 0, 0.7);
  position: relative;
}

.test__flex {
  display: flex;
  align-items: flex-start;
}

.test__count {
  color: #d62327;
  font-size: 8.38651vw;
  text-transform: uppercase;
  display: flex;
  align-items: center;
  flex-wrap: nowrap;
  flex-shrink: 0;
  margin-right: 3.37284vw;
  padding-top: 0.91158vw;
}

.test__question {
  color: #e2e3d6;
  font-size: 2.73473vw;
  line-height: 2.91705vw;
  font-family: "Bebas Neue", "ArnoPro", sans-serif;
}

.test__variants {
  margin-top: 4.55789vw;
}

.test__variant {
  min-height: 3.19052vw;
  display: block;
}

.test__variant + .test__variant {
  margin-top: 2.27894vw;
}

.test__answer {
  font-size: 2.00547vw;
  line-height: 2.3701vw;
  letter-spacing: 0.05014vw;
  cursor: pointer;
}

.test__btns {
  position: absolute;
  width: 100%;
  display: flex;
  bottom: -3.37284vw;
  left: 0;
  justify-content: center;
}

.test__btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 31.35825vw;
  height: 7.01914vw;
  cursor: pointer;
  color: #e2e3d6;
  font-family: "Bebas Neue", "ArnoPro", sans-serif;
  font-size: 4.28441vw;
  background: #000000;
  border-radius: 4.55789vw;
  box-sizing: border-box;
  border: 2px solid #000000;
  transition: 0.3s;
}

.test__btn:hover {
  border-color: #d62327;
}

.test__test {
  box-sizing: border-box;
  padding: 4.46673vw 5.01367vw 7.7484vw;
}

.test__results {
  box-sizing: border-box;
  padding: 6.83683vw 4.55789vw 4.55789vw;
}

.test__title {
  color: #e2e3d6;
  font-family: "Bebas Neue", "ArnoPro", sans-serif;
  font-size: 5.46946vw;
  text-align: center;
}

.test__title span {
  color: #d62327;
}

.test__itogo {
  margin-bottom: 4.55789vw;
  width: 62.80766vw;
  font-size: 2.18778vw;
  font-weight: 400;
  line-height: 2.55242vw;
  letter-spacing: 0.05469vw;
  margin-left: auto;
  margin-top: auto;
}

.test__share {
  margin-top: 3.64631vw;
}